.movies__card {
      cursor: pointer;
      list-style-type: none;

      /* @media screen and (min-width: 768px) {
        width: 50%;
      }
  
      @media screen and (min-width: 1280) {
        width: 30%;
      } */
    }
  
.movies__card-photo {
      margin-bottom: 10px;
      width: 280px;
      height: 402px;
      border-radius: 5px;
      transition: box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);
  
      @media screen and (min-width: 768px) {
        margin-bottom: 8px;
        width: 336px;
        height: 455px;
      }
      @media screen and (min-width: 1280px) {
        margin-bottom: 12px;
        width: 395px;
        height: 574px;
      }
    }

.movies__card-photo:hover {
        box-shadow: 0px 0px 21px 3px #b92f2c;
    }

.movies__card-title {
      margin-bottom: 4px;
      font-size: 12px;
      line-height: 1.17;
      color: #000;
      text-decoration: none;
      @media screen and (min-width: 768px) {
        line-height: 1.33;
      }
      @media screen and (min-width: 1280px) {
        font-size: 18px;
        line-height: 1.2;
      }
    }
  
.movies__card-genres {
      font-size: 12px;
      line-height: 1.17;
      color: #b92f2c;
      text-decoration: none;

      @media screen and (min-width: 768px) {
        line-height: 1.33;
      }
      @media screen and (min-width: 1280px) {
        font-size: 18px;
        line-height: 1.2;
      }
    }